.container-musics {
  display: flex;
}

.li-album {
  list-style: none;
}

.musics-album {
  align-items: center;
  background-color: white;
  border-radius: 30px;
  display: flex;
  justify-content: space-evenly;
  width: 700px;
}

.all-album {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  width: 100vw;
}

.title-album {
  background-color: white;
  box-shadow: 0 0 4px rgba(172, 181, 200, 0.938);
  height: 200px;
  position: absolute;

  /* left: 250px; */
  top: 0;
  width: 100vw;
}

.ul-album {
  margin-left: 600px;
  margin-top: 250px;
}

.img-album {
  border-radius: 12px;
  filter: drop-shadow(-9px 8px 8px rgba(68, 73, 85, 0.2));
  height: 240px;
  left: 400px;
  position: absolute;
  top: 100px;
  width: 240px;
}

.name-album {
  left: 700px;
  position: absolute;
  top: 100px;
}

.h1-album {
  color: black;
  font-size: 20px;
  font-weight: 700;
  line-height: 150%;
}

.h3-album {
  color: black;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

@media screen and ( max-width : 1150px ) {

  /* .musics-album {
    margin-left: -600px;

    /* width: 100vw; */

  .img-album {
    left: 150px;
    top: 150px;
  }

  .ul-album {
    margin: auto;
    margin-bottom: 40px;
    margin-top: 450px;
  }

  .name-album {
    left: 420px;
    position: absolute;
    top: 200px;
  }
}

@media screen and ( max-width : 720px ) {

  .img-album {
    height: 180px;
    left: 20px;
    top: 150px;
    width: 180px;
  }

  .name-album {
    left: 220px;
    position: absolute;
    top: 200px;
  }

  .ul-album {
    margin-top: 380px;
  }

  .musics-album {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    padding: 10px;
    width: 80%;
  }

  /* .all-album {
    width: 80%;
  } */

}
